@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.event_list {
  box-sizing: border-box;
  padding: 19px;
  border: 1px solid #494440;
  margin-bottom: 30px;
  text-decoration: none;
  color: $color-text01;
  width: 100%;
  &:first-child {
    margin-top: 10px;
  }
  &:last-child {
    margin-bottom: 10px;
  }
  &__top {
    width: 100%;
    margin: 0;
    display: flex;
    justify-content: space-between;
    &__left {
      flex: 0 0 300px;
      height: 225px;
      position: relative;
      img {
        height:100%;
        width: 100%;
        object-fit: contain;
        font-family: 'object-fit: contain;;';
      }
    }
    &__right {
      flex: 0 0 345px;
      position: relative;
      margin: 0;
      padding: 0;
      .title {
        @include font-line(23, 46);
        font-weight: bold;
        margin: 10px 0 25px 0;
      }
    }
  }
  &__bottom {
    .memo {
      margin: 1em 0;
    }
  }
  .blockButton {
    width: 150px;
    margin-left: auto;
    .eventButton {
      border: 1px solid #322e2c;
      color: $color-text01;
      &:hover {
        @include opacity(1);
        background: #322e2c;
        border-color: #322e2c;
        color: #fff;
      }
    }
  }
}

/*詳細ページ*/
.detail {
  padding: 0;
  .return {
    width: 150px;
    margin: 0 0 20px auto;
    padding: 0;
    display: block;
    .backButton {
      border: 1px solid #322e2c;
      color: $color-text01;
      &:hover {
        @include opacity(1);
        background: #322e2c;
        border-color: #322e2c;
        color: #fff;
      }
    }
  }
  &__heading {
    @include font-line(25, 30);
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__img {
    width: 340px;
    position: relative;
    a {
      display: block;
      position: relative;
      border: none;
      .zoomIcon {
        @include font-line(20, 20);
        display: none;
        color: #fff;
        height: 20px;
        width: 20px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 2;
      }
    }
    &:hover {
      a {
        &::before {
          background: rgba(0,0,0,0.8);
          content: "";
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          z-index: 1;
        }
        .zoomIcon {
          display: block;
        }
      }
    }
    .stamp {
      @include font-line(14, 61);
      font-weight: 700;
      background-color: $color-primary;
      border-radius: 50%;
      color: #fff;
      position: absolute;
      height: 61px;
      width: 61px;
      top: -10px;
      left: 0;
      z-index: 2;
      text-align: center;
    }
    .memo {
      @include font-line(16);
      padding: 8px 0;
    }
  }
  &__overview {
    width: 341px;
    margin-top: 30px;
    .mapButton {
      width: 150px;
      margin-left: auto;
      margin-top: 20px;
      .button {
        @include font-line(14, 14);
        border-radius: 20px;
        i {
          padding-right: 10px;
        }
      }
    }
  }
  &__content {
    margin-top: 15px;
  }
  .photoList {
    $photoList: '.photoList';
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 700px;
    a {
      text-decoration: none;
      border: none;
      color: $color-text01;
    }
    &::after{
      content: "";
      display: block;
      width: calc(33.3333333% - 12px);
    }
    &__item {
      flex: 0 0 220px;
      margin-bottom: 20px;
      &:hover {
        #{$photoList}__photo {
          &::before {
            background: rgba(0,0,0,0.8);
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            z-index: 1;
          }
          .zoomIcon {
            display: block;
          }
        }
      }
    }
    &__photo {
      width: 100%;
      padding-bottom: 100%;
      position: relative;
      overflow: hidden;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        z-index: 0;
        max-width: 100%;
        max-height: 100%;
      }
      .zoomIcon {
        @include font-line(20, 20);
        display: none;
        color: #fff;
        height: 20px;
        width: 20px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 2;
      }
    }
    &__title {
      margin: 7px 0;
    }
  }
}
